import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

// import Custom Components
import MainMenu from './partials/main-menu';
import CartMenu from './partials/cart-menu';

import LoginModal from '../features/modal/login-modal';

import { isIEBrowser } from '../../utils';
import { showModal } from '../../actions';

function Header( props ) {
    const { wishlist, logo = "assets/images/logo.png", container = "container" } = props;

    function openLoginModal( e ) {
        props.showModal( 'login' );
        e.preventDefault();
    }

    return (
        <header className="header header-6">
            <div className="header-top">
                <div className={ container }>
                    <div className="header-left">
                        <ul className="top-menu top-link-menu d-none d-md-block">
                            <li>
                                <Link to="#">Links</Link>
                                <ul>
                                    <li><Link to="tel:#"><i className="icon-phone"></i>Call: +0123 456 789</Link></li>
                                </ul>
                            </li>
                        </ul>
                    </div>

                    <div className="header-right">
                        <div className="social-icons social-icons-color">
                            <Link to="#" className="social-icon social-facebook" title="Facebook" target="_blank"><i className="icon-facebook-f"></i></Link>
                            <Link to="#" className="social-icon social-twitter" title="Twitter" target="_blank"><i className="icon-twitter"></i></Link>
                            <Link to="#" className="social-icon social-instagram" title="Pinterest" target="_blank"><i className="icon-instagram"></i></Link>
                            <Link to="#" className="social-icon social-pinterest" title="Instagram" target="_blank"><i className="icon-pinterest-p"></i></Link>
                        </div>
                        <ul className="top-menu top-link-menu">
                            <li>
                                <Link to="#">Links</Link>
                                <ul>
                                    <li><Link to="#signin-modal" data-toggle="modal" onClick={ openLoginModal }><i className="icon-user"></i>Login</Link></li>
                                </ul>
                            </li>
                        </ul>

                        <div className="header-dropdown">
                            <Link to="#">USD</Link>
                            <div className="header-menu">
                                <ul>
                                    <li><Link to="#">Eur</Link></li>
                                    <li><Link to="#">Usd</Link></li>
                                </ul>
                            </div>
                        </div>

                        <div className="header-dropdown">
                            <Link to="#">Eng</Link>
                            <div className="header-menu">
                                <ul>
                                    <li><Link to="#">English</Link></li>
                                    <li><Link to="#">French</Link></li>
                                    <li><Link to="#">Spanish</Link></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="header-middle">
                <div className={ container }>
                    <div className="header-left">
                        <div className="header-search header-search-extended header-search-visible d-none d-lg-block">
                            <Link to="#" className="search-toggle" role="button"><i className="icon-search"></i></Link>
                            <form action="#" method="get">
                                <div className="header-search-wrapper search-wrapper-wide">
                                    <label htmlFor="q" className="sr-only">Search</label>
                                    <button className="btn btn-primary" type="submit"><i className="icon-search"></i></button>
                                    <input type="search" style={ isIEBrowser() ? { paddingLeft: '30px' } : {} } className="form-control" name="q" id="q" placeholder="Search product ..." required />
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="header-center">
                        <Link to={ `${process.env.PUBLIC_URL}/` } className="logo">
                            <img src={ `${process.env.PUBLIC_URL}/${logo}` } alt="Molla Logo" width="82" height="20" />
                        </Link>
                    </div>

                    <div className="header-right">
                        <Link to={ `${process.env.PUBLIC_URL}/shop/wishlist` } className="wishlist-link">
                            <i className="icon-heart-o"></i>
                            <span className="wishlist-count">{ wishlist.length }</span>
                            <span className="wishlist-txt">My Wishlist</span>
                        </Link>
                        <CartMenu />
                    </div>
                </div>
            </div>

            <div className="header-bottom sticky-header" >
                <div className={ container }>
                    <div className="header-left">
                        <MainMenu />
                        <button className="mobile-menu-toggler">
                            <span className="sr-only">Toggle mobile menu</span>
                            <i className="icon-bars"></i>
                        </button>
                    </div>

                    <div className="header-right">
                        <i className="la la-lightbulb-o"></i><p>Clearance Up to 30% Off</p>
                    </div>
                </div>
            </div>

            <LoginModal />
        </header>
    )
}

function mapStateToProps( state ) {
    return {
        wishlist: state.wishlist.list
    }
}

export default ( connect )( mapStateToProps, { showModal } )( Header );